            this.scrollTab();
        },
        scrollHack() {
            document.getElementById("scheme-container").scrollIntoView();
            this.scrollTab();
        },
        getPath() {
            let tabIndex = this.$route.query.tabIndex;
            if (tabIndex != undefined) {
                // this.$router.push({ query: {} }); // 
                this.getChangTab(tabIndex);
                this.scrollHack();
                this.$refs.Tab.changeActive(parseInt(tabIndex));
            }
        },
        scrollTab() {
            this.$nextTick(() => {
                // 
                let box = document.getElementById("tabUl");
                let scrollLeft = box.querySelector(".active").offsetLeft;
                box.scrollLeft = scrollLeft - 100;
            });
        },
