    color: #788398;
    line-height: 50px;
    padding-bottom: 80px;
    padding-left: 20px;

    li {
        text-align: left;
        position: relative;
        box-sizing: border-box;
        &::before {
            content: ".";
            border-radius: 50%;
            position: absolute;
            top: 16px;
            left: -20px;
            line-height: 4px;
        }
    }
}
</style>



